.playerComponent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align:center;
    background-color: rgb(226, 226, 226);
    width: 25%;
    max-width: 600px;
    margin: 1em auto;
    padding-bottom: 2em;
    /*border: 0.1px solid black;*/
    border-radius: 10px;
  }
  
  .musicCover {
    border-radius: 10%;
    width: 200px;
  }
  
  .playButton {
    background: none;
    border: none;
    align-items: center;
    justify-content: center;
  }
  
  .infoMusic, .coverImage, .controleButton, .timeBar {
    margin-top: 1em;
  }
  
  .subTitle {
    color: #4f4f4f;
  }
  
  .time {
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    color: #828282;
    font-size: smaller;
  }
  
  .timeline {
    width: 80%;
    background-color: #27ae60;
  }
  
  input[type="range"] {
    background-color: #27ae60;
  }
  
  @media (max-width: 900px) {
    .playerComponent {
      width: 50%;
    }
  }